import React from 'react';
import styles from './WeaponView.scss';
import classNames from 'classnames/bind';
import NumberFormat from 'react-number-format';
import { Helmet } from "react-helmet";
import {Link} from 'react-router-dom';

const cx = classNames.bind(styles);

const WeaponView = ({itemInfo, currWeaponUpDv, pathname}) => {
  const {
    item_nm, img_src, item_dtl_dv, dmg, cri, speed, tier, size, illegal, _id
  } = itemInfo;
  const {bodyUp, dmgUp} = currWeaponUpDv;
  const titleDefaultText = ' ';  
  let titleText = '';
  let costomLinkMsg = '';
  let metaText = ` .   .  .   `;
  
  if(item_nm === undefined || item_nm === null || item_nm === '') {
    if(pathname.indexOf('custom') > -1) {
      titleText = ' - ' + titleDefaultText;
    }else{
      titleText = titleDefaultText;
    }
  }else{
    if(pathname.indexOf('custom') > -1) {
      titleText = item_nm + ' -  - ' + titleDefaultText;
    }else if(pathname.indexOf('wp') > -1) {
      titleText = item_nm + ' -  - ' + titleDefaultText;
      costomLinkMsg = <Link to={`/custom/${_id}`} className={cx('weapon-dmgsim-txt')}></Link>;
    }
  }

  return (
    <div className={cx('weapon-view')}>
      <Helmet>
        <title>{titleText}</title>
        <meta name="description" content={metaText} />
      </Helmet>
      <h2 className={cx('name')}>
        {item_nm === undefined ?
          <Link to='/wp'></Link> : 
          <>{item_nm} <Link to='/wp' className={cx('weapon-change-txt')}></Link> {costomLinkMsg}</> 
        }
      </h2>
      <div className={cx('item-info')}>
        <div className={cx('img')}><img src={img_src} alt={item_nm}/></div>
        <div className={cx('option')}>
          <div className={cx(`body_nm${bodyUp}`)}>
            {
              (() => {
                const dmgUpNum = Number(dmgUp);
                if(dmgUpNum === 0) return '';
                else if(dmgUpNum > 0 && dmgUpNum < 10) return `+${dmgUp} `;
                else if(dmgUpNum === 10) return 'MAX ';
                else if(dmgUpNum > 10) return `MAX +${dmgUp-10} `;
              })()
            }
            {
              (() => {
                switch(bodyUp) {
                  case '0': return '';
                  case '1': return '';
                  case '2': return '';
                  case '3': return '';
                  case '4': return '';
                  case '5': return '';
                  case '6': return 'O.T.';
                  default: return '';
                }
              })()
            }
          </div>
          <div className={cx('dmg')}>
            :<NumberFormat value={dmg} displayType={'text'} thousandSeparator={true} prefix={''} />
          </div>
          <div className={cx('cri')}>:{cri}</div>          
        </div>
        <div className={cx('sub_option')}>
          <div className={cx('tier')}>{tier}</div>
          <div className={cx('detail_dv')}>{item_dtl_dv}</div>
          <div className={cx('size')}>{size}</div>
          <div className={cx('speed')}>{speed}/1</div>
          <div className={cx('illegal')}>{illegal === 'Y' ? '' : ''}</div>
        </div>
      </div>
    </div>
  );
};

export default WeaponView;